import React, { useState } from 'react';
import '../App.css';
import todo from '../images/todo.png';

const Todo = () =>{

  const [inputData, setinputData] = useState('');
  const [items, setItems] = useState([]);
  const [toggleSubmit, setToggleSubmit] = useState(true);
  const [isEditItem, setIsEditItem] = useState(null);

  const addItem = () => {
      if(!inputData){
        alert('plzz fill data');
      } else if (inputData && !toggleSubmit) {
        setItems(
          items.map((elem) =>{
            if( elem.id === isEditItem ){
              return{...elem, name:inputData}
            }
            return elem;
          })
        )
          setToggleSubmit(true);
          setinputData('');
          setIsEditItem(null);
      } else{
     const allInputData = { id: new Date().getTime().toString(), name:inputData }
     setItems([...items ,allInputData]);
     setinputData('');
      }
  }

//   delete the items
  const deleteItem = (index) =>{
    
     const updateditems = items.filter((elem) =>{
       return index !== elem.id;
     })
     setItems(updateditems);
  }

//Edit Item
//When user click on edit <button>
// 1: get the id and name of the data which user clicked to edit
// 2: set the toggle mode to change the submit button into edit button
// 3: now update the value of the setInput with the new updated value to edit
// 4: to pass the current element Id to new state variable for reference

const editItem = (id) =>{
   let newEditItem = items.find((elem) =>{
      return elem.id === id;
   })
   console.log(newEditItem);
   setToggleSubmit(false);
   setinputData(newEditItem.name);
   setIsEditItem(id);
}

//   remove all items
  const removeAll = () =>{
      setItems([]);
  }

  return(
     <>
       <div className="main-div">
          <div className="child-div">
             <figure>
                 <img src={todo} alt="todologo" />
                 <figcaption> Add Your List Here üëç </figcaption>
             </figure>

             <div className="addItems">
                <input type="text" placeholder="‚úçÔ∏è Add Items..."
                value={inputData}
                onChange={(e) => setinputData(e.target.value)}
                />
                {
                  toggleSubmit ? <i className="fas fa-plus fa-2x add-btn" title="Add Item" onClick={addItem}></i> :
                  <i className="far fa-edit fa-2x add-btn" title="Update Item" onClick={addItem}></i>
                }
                
             </div>
            <br/>
            <div className="showItems">
              {
                items.map((elem) =>{
                   return(
                    <div className="eachItem" key={elem.id}>
                      <h3>{elem.name}</h3>
                      <br />
                      <div className="todo-btn">
                      <i className="far fa-edit add-btn" title="Edit Item" onClick={() => editItem(elem.id)}></i>
                      <i className="far fa-trash-alt del-btn" title="Delete Item" onClick={() => deleteItem(elem.id)}></i>
                      </div>
                   </div>
                   )
                })
              }

            </div>
            <br />
            {/* clear all button */}
           <div className="showItems">
                <button className="btn effect04" data-sm-link-text="Remove All" onClick={removeAll}><span> Remove All </span></button>
           </div> 
          </div>
       </div>
     </>
  );
}

export default Todo;